import React, { useContext } from 'react'
import { ApplicationContext } from '../lib'
import Toolbar from '../components/toolbar'
import { GiShirt, GiSmartphone, GiCityCar, GiGlassCelebration, GiVendingMachine } from 'react-icons/gi'
import { NavLink, Redirect } from 'react-router-dom'
import links from '../lib/links'
import { ErrorPage } from './error'

export const Profile = () => {
    const ctx = useContext(ApplicationContext)
    const [current, update] = React.useState<number>(0)

    if (!ctx.user || ctx.user.isAnonymous) {
        return <ErrorPage>You must be authenticated to view this page!</ErrorPage>
    }

    return (
        <div style={ctx.theme.main}>
            <Toolbar />
            <div className='container'>
                <div className='section'>
                    <div className='card'>
                        <div className='card-content'>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}